	module.exports = {"data":{"wordpressPost":{"title":"New generation electronic games boosts kids’ physical activity at home","content":"<p><a title=\"Full text\" href=\"http://www.bmjopen.bmj.com/lookup/doi/10.1136/bmjopen-2013-002629\" target=\"_blank\">Giving kids new generation “active” electronic games boosts their physical activity at home and has the same effect as stopping them from using the older versions altogether</a>, indicates a small study published in <a title=\"BMJ Open\" href=\"http://bmjopen.bmj.com/\" target=\"_blank\">BMJ Open.</a></p>\n<p>Most electronic games are no better than watching TV in terms of the body movement and energy expenditure involved, say the authors. Kids in developed countries spend an estimated 38 to 90 minutes a day playing these games.</p>\n<p>But what has not been clear is whether the newer generation “active” games, such as Sony PlayStation EyeToy and Move, dance mats, and Microsoft Xbox Kinect, are any better.</p>\n<p>The Australian researchers compared the impact of removing traditional electronic games, involving a game pad, from the home or replacing them with more active newer generation versions among 56 ten to 12 year olds over a total of three years (2007-2010).</p>\n<p>They wanted to see if either approach boosted kids’ daily physical activity levels and/or curbed the amount of sedentary time they spent in front of a screen.</p>\n<p>For a period of 8 weeks, the daily use of electronic games at home was banned, while levels of moderate to vigorous physical activity were measured using a portable device (accelerometer) worn on the hip.</p>\n<p>This was followed by two identical periods of time during which the children were given daily access to traditional sedentary electronic games or active versions.</p>\n<p>The children also kept 7-day diaries of the main activities they did for each 30 minute period during waking hours.</p>\n<p>The diaries showed that they spent a daily average of 1.5 hours on active leisure and transport and 4.5 hours on all sedentary leisure, of which screen time made up more than half.</p>\n<p>Watching TV made up the largest chunk of screen time, at an average of 107 minutes every day, followed by sedentary electronic games at an average of 44 minutes, and non-gaming computer use (24 minutes).</p>\n<p>Levels of physical activity across the week didn’t vary much for any of the three periods. But compared with after school access to traditional electronic games, removal of all devices increased daily moderate to vigorous physical activity by almost 4 (3.8 ) minutes and decreased sedentary time by almost 5 (4.7) minutes.</p>\n<p>Giving the children access to active electronic games after school also significantly boosted daily physical activity levels by just over 3 minutes and reduced sedentary time by just over 6 minutes.</p>\n<p>These differences may seem small, say the authors, and are unlikely to have any clinical impact by themselves. But they are significant, because of the rapidly increasing levels of exposure children have to electronic gaming on computers, tablets and smart phones, in addition to internet surfing and social media, they say.</p>\n<p>“Therefore small changes across a variety of these platforms could result in a more substantial clinical impact,” they write, adding: “While our study focused on the home setting, school offers another opportunity for more active technologies…such as sit-stand desks or active-input electronic media as part of lessons.”</p>\n<p>Furthermore, substituting older style electronic games with newer generation active versions may be easier for both kids and their parents to sustain than an outright ban, they conclude.<!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"2c5d2d62-360c-52d4-b15f-fb1febcf629e"}}

